.watch {
  background-color: #fff;
  border: 3px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.watch__center {
  z-index: 2;
  background-color: orange;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  position: absolute;
}

.watch__hand-sec, .watch__hand-min {
  z-index: 1;
  transform-origin: 50% 100%;
  animation-name: move;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-fill-mode: backwards;
  position: absolute;
}

.watch__hand-sec {
  border: 3px solid green;
  height: 50%;
  animation-duration: 5s;
  top: 0;
}

.watch__hand-min {
  border: 4px solid #00f;
  height: 25%;
  animation-duration: 60s;
  top: 25%;
}

@keyframes move {
  100% {
    transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

